@import "../../style.scss";

.comments {
    @include themify($themes) {


        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }

        .write{
            display: flex;
            align-content: center;
            justify-content: space-between;
            gap: 20px;
            margin: 20px 0px;

            input{
                flex: 5;
                padding: 10px;
                border: 1px solid themed("border");
                background-color: transparent;
                color: themed("textColor");
            }

            button{
                border: none;
                background-color: #5271ff;
                color: white;
                padding: 10px;
                cursor: pointer;
                border-radius: 3px;
            }
        }

        .comment {
            margin: 10px 0px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;

            .info {
                flex: 5;
                display: flex;
                flex-direction: column;
                gap: 5px;
                align-items: flex-start;


                span {
                    font-weight: bold;
                }

                p {
                    color: themed("textColor");
                    margin-top: 0px;

                }
            }

            .date{
                flex: 1;
                align-self: center;
                color: gray;
                font-size: 14px;
            }
        }

    }
}